export const productreducer = (state = productinital, { type, payload }) => {
  switch (type) {
    case ADD_PRODUCT:
      return [...state, { ...payload }];
    case DELETE_PRODUCT:
      return [...state.slice(0, payload), ...state.slice(payload + 1)];
    case ADD_ONE:
      const addoneafter = [...state];
      addoneafter[payload].addtocart += 1;
      return addoneafter;
    case DELETE_ONE:
      const deleteoneafter = [...state];
      deleteoneafter[payload].addtocart -= 1;
      return deleteoneafter;
    case OPEN_QTY_NUMBER:
      const openqty = [...state];
      openqty[payload].isaddqty = true;
      return openqty;
    case CLOSE_QTY_NUMBER:
      const closeqty = [...state];
      closeqty[payload].isaddqty = false;
      return closeqty;
    case CHANGE_THE_QTY:
      const changeqtyafter = [...state];
      changeqtyafter[payload.index].addtocart = payload.value;
      return changeqtyafter;
    case OPEN_DETAIL:
      const opendetail = [...state];
      opendetail[payload].isdetail = true;
      return opendetail;
    case CLOSE_DETAIL:
      const closedetail = [...state];
      closedetail[payload].isdetail = false;
      return closedetail;
    default:
      return state;
  }
};
